* {
  margin: 0;
  padding: 0;
}

.App {
  overflow-x: hidden;
  overflow-y: auto;
}

.Home {
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-box {
  display: flex;
  row-gap: .1cm;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 20cm;
}

.tagline {
  font-size: 20px;
  color: white;
  text-align: center;
  font-family: Wellfleet;

}

.name {
  font-size: 90px;
  font-family: Wellfleet;
  color: white;
  font-weight: 600;
  letter-spacing: .08cm;
}

.Home {
  position: relative;
  width: 100vw;
  height: 100vh;
}

.Home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("me.jpg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center center;
  filter: brightness(23%);
  /* Adjust the value as needed (0.7 reduces brightness) */
}



@media only screen and (max-width : 918px) {

  .Home::before {
    background-size: auto 100%;
  }
}